@use "../../styles/variables";

.container {
  max-width: variables.$contentWidth;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: variables.$topPadding 15px 50px;
  display: flex;
  flex-direction: column;

  @media(max-width: variables.$collapseWidth) {
    justify-content: space-between;
    width: 100%;
    align-items: start;
    padding-left: 0;
    padding-right: 0;
    padding-top: calc(variables.$headerHeight + 40px);
    background-color: variables.$background;

    nav {
      &:last-child {
        display: none;
      }
      &:nth-last-child(2) {
        margin-right: 0;
      }
      text-transform: uppercase;
    }
  }
}

.sideGlow {
  position: fixed;
  width: 311px;
  height: 311px;
  left: -102px;
  top: 26%;

  background: #961A1A;
  opacity: 0.5;
  filter: blur(196px);
  z-index: 0;
}

.topGlow {
  position: fixed;
  width: 259px;
  height: 259px;
  left: 42%;
  top: -154px;

  background: #833AB4;
  opacity: 0.5;
  filter: blur(196px);
  z-index: 0;

  @media (max-width: variables.$collapseWidth) {
    left: 80%;
    top: 107px;
  }
}

.purpleBall {
  position: fixed;
  width: 60px;
  height: 60px;
  left: 60%;
  top: -2px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(236, 208, 255, 0.64) 0%, rgba(161, 93, 207, 0.24) 59.67%, rgba(118, 49, 164, 0.208) 78.59%, rgba(86, 24, 128, 0.232) 100%);
  box-shadow: 3px 6px 27px -10px rgba(177, 118, 217, 0.25);
  filter: blur(5px);
  backdrop-filter: blur(2px);
  border-radius: 100%;
  z-index: 0;

  @media(max-width: variables.$collapseWidth) {
    right: -30px;
    left: unset;
    top: 236px;
  }
}

.largeRedBall {
  position: absolute;
  width: 83px;
  height: 83px;
  left: 36%;
  top: 83px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.536) 0%, rgba(212, 93, 93, 0.264) 59.67%, rgba(167, 49, 49, 0.248) 78.59%, rgba(130, 25, 25, 0.248) 100%);
  box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
  backdrop-filter: blur(2px);
  border-radius: 100%;

  @media (max-width: variables.$collapseWidth) {
    left: 7%;
    top: 450px;
  }
}

.smallRedBall {
  position: absolute;
  width: 28px;
  height: 28px;
  left: 59%;
  top: 547px;

  background: radial-gradient(64% 64% at 27.25% 26.5%, rgba(255, 208, 208, 0.48) 0%, rgba(212, 93, 93, 0.248) 59.67%, rgba(167, 49, 49, 0.264) 78.59%, rgba(130, 25, 25, 0.232) 100%) /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */;
  box-shadow: 3px 6px 27px -10px rgba(223, 125, 125, 0.25);
  filter: blur(2.5px);
  backdrop-filter: blur(2px);
  border-radius: 100%;
}
